<template>
  <div class="edit_book">
    <div class="nav">
      <a-breadcrumb>
        <a-breadcrumb-item><router-link to="/admin/home">首页</router-link></a-breadcrumb-item>
        <a-breadcrumb-item><router-link to="/admin/book/manage">书籍管理</router-link></a-breadcrumb-item>
        <a-breadcrumb-item>编辑书籍{{$route.params.id}}</a-breadcrumb-item>
      </a-breadcrumb>
    </div>
    <book-form
      :detail="bookDetail"
      @submit="handleUpdate"
    />
  </div>
</template>
<script>
import BookForm from '@/components/BookForm.vue'
import { mapActions, mapState } from 'vuex'
export default {
  name: 'EditBook',
  components: { BookForm },
  computed: {
    ...mapState('booksManage', ['bookDetail'])
  },
  methods: {
    ...mapActions('booksManage', {
      getBookDetail: 'getBookDetail',
      upadateBooks: 'upadateBooks'
    }),
    handleUpdate(val) {
      this.upadateBooks({
        id: this.$route.params.id,
        data: val
      }).then(res => {
        this.$message.success(res.message)
        this.$router.push({
          name: 'Book Manage'
        })
      })
    }
  },
  created() {
    this.getBookDetail(this.$route.params.id)
  }
}
</script>