<template>
  <div class="book_info">
    <h2>书籍信息</h2>
    <a-form
      labelAlign="right"
    >
      <a-row type="flex" class="layout_row" :gutter="[32]">
        <template v-for="(item, index) in formItem">
          <template v-if="item.val === 'classify'">
            <a-col
              :key="index"
              :sm="24"
              :lg="12"
              :xl="8"
            >
              <a-form-item :label="item.label" required>
                <a-select
                  v-model="params[item.first]"
                  :options="firstCateOptions"
                  placeholder="一级分类"
                  allowClear
                ></a-select>
                <a-select
                  v-model="params[item.second]"
                  :options="secondCateOptions"
                  placeholder="二级分类"
                  allowClear
                ></a-select>
              </a-form-item>
            </a-col>
          </template>
          <template v-else-if="item.val === 'image'">
            <a-col
              :key="index"
              :sm="24"
            >
              <a-form-item :label="item.label" required>
                <a-upload
                  name="file"
                  list-type="picture-card"
                  class="avatar-uploader"
                  :show-upload-list="false"
                  :action="`${proxy}${prefix}/upload/upload`"
                  @change="handleImageChange"
                >
                  <div
                    v-if="params.image"
                    class="bg_img book_img"
                    :style="`background-image: url(${prefix}/cbh_file/${params.image});`"
                  ></div>
                  <div v-else>
                    <a-icon :type="imgLoading ? 'loading' : 'plus'" />
                    <div class="ant-upload-text">
                      Upload
                    </div>
                  </div>
                </a-upload>
              </a-form-item>
            </a-col>
          </template>
          <template v-else-if="item.val === 'introduction'">
            <a-col
              :key="index"
              :sm="24"
              :lg="12"
              :xl="8"
            >
              <a-form-item :label="item.label" required>
                <a-input
                  type="textarea" 
                  v-model="params[item.val]"
                  :rows="5"
                />
              </a-form-item>
            </a-col>
          </template>
          <template v-else>
            <a-col
              :key="index"
              :sm="24"
              :lg="12"
              :xl="8"
            >
              <a-form-item :label="item.label" required>
                <a-input v-model="params[item.val]" />
              </a-form-item>
            </a-col>
          </template>
        </template>
      </a-row>
    </a-form>
    <div style="padding: 0 30px;">
      <a-button
        type="primary"
        style="margin-right: 20px;"
        @click="handleSubmit"
      >
        创建
      </a-button>
      <a-button type="danger" @click="cancleSubmit">取消</a-button>
    </div>
  </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'BookForm',
  components: {  },
  props: {
    detail: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    let params = {
          book_name: '',
          author: '',
          first_category_id: undefined,
          second_category_id: undefined,
          stock: '',
          book_code: '',
          place: '',
          image: '',
          introduction: ''
        }

    const formItem = [
      { label: '书籍名称', val: 'book_name' },
      { label: '书籍作者', val: 'author' },
      { label: '书籍分类', val: 'classify', first: 'first_category_id', second: 'second_category_id' },
      { label: '馆藏量', val: 'stock' },
      { label: '书籍编码', val: 'book_code' },
      { label: '书籍位置', val: 'place' },
      { label: '书籍图片', val: 'image' },
      { label: '书籍简介', val: 'introduction' },
    ]

    return {
      params,
      formItem,
      imageUrl: '',
      imgLoading: false,
      proxy: process.env.VUE_APP_API_URL,
      prefix: process.env.VUE_APP_PREFIX,
    }
  },
  computed: {
    ...mapState('booksManage', ['allCategoryList']),
    secondCateOptions() {
      return this.allCategoryList && 
        this.allCategoryList.second && 
        this.allCategoryList.second.filter(item => item.pid === this.params.first_category_id)
        .map(item => {
          return {
            label: item.category_name,
            value: item.id
          }
        })
    },
    firstCateOptions() {
      return this.allCategoryList && this.allCategoryList.first && this.allCategoryList.first.map(item => {
          return {
            label: item.category_name,
            value: item.id,
          }
        })
    }
  },
  watch: {
    detail() {
      this.params = Object.assign({}, this.params, this.detail)
    }
  },
  methods: {
    handleSubmit() {
      this.$emit('submit', this.params)
    },
    cancleSubmit() {
      this.$router.push('/admin/book/manage')
      console.log('cancel submit')
    },
    handleImageChange(file) {
      if (file.file.status === 'uploading') {
        this.params.image = ''
        this.imgLoading = true
      }
      if (file.file.status === 'done') {
        this.params.image = file.file.response.data
        this.imgLoading = false
      }
    },
  },
  created() {
    this.params = Object.assign({}, this.params, this.detail)
  }
}
</script>
<style lang="scss" scoped>
.book_info {
  .layout_row {
    padding: 0 30px;
    >.ant-col {
      .ant-form-item {
        width: 100%;
        /deep/.ant-form-item-control-wrapper {
          width: 100%;
        }
        .ant-select {
          width: 50%;
        }
        .avatar-uploader {
          /deep/.ant-upload {
            width: 250px;
            height: 200px;
          }
        }
      }
    }
  }
  .book_img {
    width: 100%;
    height: 100%;
  }
}
</style>